<template>
  <div class="alert" v-bind:class="classes" v-show="value">
    <!-- <v-icon class="alert__icon" v-if="!hideIcon">{{ mdIcon }}</v-icon> -->
    <div>
      <slot></slot>
    </div>
    <a class="alert__dismissible" href="#!" v-if="dismissible" v-on:click.prevent="$emit('input', false)">
      <!-- <v-icon right="right">cancel</v-icon> -->
    </a>
  </div>
</template>

<script>
export default {
  name: 'alert',

  props: {
    dismissible: Boolean,

    error: Boolean,

    hideIcon: Boolean,

    icon: String,

    info: Boolean,

    success: Boolean,

    warning: Boolean,

    value: {
      type: Boolean,
      default: true
    }
  },

  computed: {
    classes() {
      return {
        'alert--dismissible': this.dismissible,
        'alert--error': this.error,
        'alert--info': this.info,
        'alert--success': this.success,
        'alert--warning': this.warning,
      }
    },

    mdIcon() {
      if (this.icon) {
        return this.icon
      }

      switch (true) {
        case this.error:
          return 'warning'
          break
        case this.info:
          return 'info'
          break
        case this.success:
          return 'check_circle'
          break
        case this.warning:
          return 'priority_high'
          break
      }
    }
  }
}
</script>


